import listCell from '@/components/com-input';
export default {
	components: {
		listCell
	},
	data() {
		return {
			mobile: '',
			code: '',
			logining: false,
			sending: false,
			sendTime: '获取验证码',
			count: 60
		};
	},
	onLoad() {},
	methods: {
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},

		countDown() {
			const {
				count
			} = this;
			if (count === 1) {
				this.count = 60;
				this.sending = false;
				this.sendTime = '获取验证码';
			} else {
				this.count = count - 1;
				this.sending = true;
				this.sendTime = count - 1 + '秒后重新获取';
				setTimeout(this.countDown.bind(this), 1000);
			}
		},

		sendMsg() {
			const {
				mobile
			} = this;
			if (!mobile) {
				this.$queue.showToast('请输入手机号');
			} else if (mobile.length !== 11) {
				this.$queue.showToast('请输入正确的手机号');
			} else {
				this.$queue.showLoading('正在发送验证码...');
				this.$Request.getT('/user/sendMsg/' + mobile + '/bind').then(res => {
					if (res.status === 0) {
						this.sending = true;
						this.$queue.showToast('验证码发送成功请注意查收');
						this.$refs.runCodes.$emit('runCodes');
						uni.hideLoading();
					} else {
						uni.hideLoading();
						uni.showModal({
							showCancel: false,
							title: '短信发送失败',
							content: res.msg,
						});
					}
				});
			}
		},
		toLogin() {
			const {
				mobile,
				code
			} = this;
			let userId = this.$queue.getData('userId');
			if (!mobile) {} else if (!code) {} else {
				this.$queue.showLoading('正在绑定中...');
				this.$Request.postT('/user/updatePhone?msg=' + code + '&phone=' + mobile + '&userId=' + userId).then(res => {
					if (res.status === 0) {
						this.$queue.setData('mobile', mobile);
						uni.switchTab({
							url: '/pages/member/user'
						});
					} else {
						uni.showModal({
							showCancel: false,
							title: '绑定失败',
							content: res.msg
						});
					}
					uni.hideLoading();
				});
			}
		}
	}
};
